a {
  color: #52b0d8; }

.b-body {
  background-color: #fafafa;
  color: #666; }

.b-wrap {
  max-width: 1200px;
  padding: 10px;
  margin: 20px auto; }

.b-rating {
  font-size: 11px;
  color: #fac343; }

.b-thumb {
  margin: 0;
  padding: 0;
  overflow: hidden; }
  .b-thumb img {
    display: block;
    min-height: 100%;
    vertical-align: top; }

.b-navpanel {
  margin: 20px auto;
  padding: 0;
  list-style: none;
  border: 1px solid #ebebeb;
  display: flex;
  justify-content: space-around;
  text-align: center; }
  .b-navpanel__item {
    width: 100%;
    position: relative;
    padding: 0 22px; }
    .b-navpanel__item:not(:first-child):before {
      content: '';
      position: absolute;
      left: 0;
      top: 12px;
      width: 2px;
      height: 17px;
      background-color: #d3d3d3; }
  .b-navpanel__link {
    display: block;
    text-decoration: none;
    border-bottom: 5px solid transparent;
    padding: 12px 0;
    transition: color .2s ease, border-bottom-color .2s ease; }
    .b-navpanel__item_active .b-navpanel__link, .b-navpanel__link:hover {
      border-bottom-color: #52b0d8;
      color: #666; }

.b-partner-list {
  text-align: center;
  font-size: 14px; }
  .b-partner-list__title {
    font-weight: normal;
    font-size: 1em; }
  .b-partner-list__content {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-between;
    font: 0/0 a; }
  .b-partner-list__item {
    width: 100px;
    height: 40px;
    margin: 0 0 10px;
    text-align: center;
    line-height: 40px;
    background-color: #fff;
    border-radius: 5px; }
    .b-partner-list__item img {
      vertical-align: middle;
      width: 80%; }

.b-btn {
  display: block;
  padding: 15px;
  text-align: center;
  background-color: #fed130;
  border: none;
  width: 100%;
  border-radius: 5px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.4);
  color: #666;
  font-size: 14px;
  transition: box-shadow .1s ease, transform .1s ease-in;
  outline: none; }
  .b-btn:hover {
    background-color: #fecb17; }
  .b-btn:active {
    transform: translate(0, 1px);
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);
    background-color: #fecb17; }

.b-form__name {
  text-align: center;
  font-weight: normal;
  font-size: 16px; }

.b-form__row {
  display: flex;
  width: 100%;
  margin: 10px auto;
  justify-content: space-between; }

.b-form__control {
  width: 100%;
  flex: 1; }
  .b-form__control input, .b-form__control select {
    display: block;
    width: 100%; }

.b-hotel-card__title {
  display: inline-block;
  vertical-align: top;
  margin: 0 0 10px;
  font-size: 16px;
  font-weight: normal; }
  .b-hotel-card__title + .b-rating {
    display: inline-block;
    vertical-align: middle;
    margin: 0 10px; }

.b-hotel-card__location {
  font-weight: normal;
  font-size: 14px; }
  .b-hotel-card__location__map-link {
    margin: 0 10px; }

.b-hotel-card__content {
  overflow: hidden; }

.b-hotel-card__figure-holder, .b-hotel-card__form-holder, .b-hotel-card__meta-holder {
  float: left;
  width: 33.33%;
  height: 420px; }

.b-hotel-card__figure-holder {
  overflow: hidden; }
  .b-hotel-card__figure-holder .b-thumb {
    border-radius: 10px 0 0 10px;
    min-height: 100%; }

.b-hotel-card__form-holder {
  color: #fff;
  background-color: #52b0d8;
  padding: 20px 40px 0; }

.b-review__bubble {
  background-color: #ebebeb;
  border-radius: 5px;
  text-align: center;
  padding: 12px;
  font-weight: bold; }
  .b-review__bubble mark {
    background: none;
    color: #65b221; }

.b-review__counter {
  text-align: right; }
  .b-review__counter__text {
    display: inline-block;
    vertical-align: top;
    position: relative;
    padding: 10px 0 0 29px; }
    .b-review__counter__text:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 20px 16px 0;
      border-color: transparent #ebebeb transparent transparent;
      transform: skew(25deg); }

.b-facility-list__item {
  display: inline-block;
  vertical-align: top;
  padding: 5px 12px;
  border: 1px solid;
  border-radius: 5px;
  margin: 0 0 5px; }

.b-facility-list__link {
  float: right;
  margin: 15px 0 0; }

html,
body {
  height: 100%; }

.b-site-container {
  display: flex;
  min-height: 100vh;
  flex-direction: column; }

.b-content {
  flex: 1; }

@media all and (-ms-high-contrast: none) {
  .b-site-container, .b-content {
    display: block; }
  *::-ms-backdrop, .b-site-container, .b-content {
    display: block; } }
